<template>
	<div class="outerW">
		<my-header3 :fatherPage="getText"></my-header3>	
		<div class="dataBox clearfix">
			<div class="dataList" v-for="(item,index) in getStateData" @click="toGoodsDataList(index)">
				<p class="dataImg">
					<img :src="item.goodsListImg" alt="">
				</p>
				<p class="dataName">
					{{item.goodsName}}
				</p>
				<p class="dataPrice">
					￥{{item.price}}
				</p>
			</div>	
		</div>
		<my-loading :loadingBol="loadingPic"></my-loading>	
		<my-pop :getTips1="popText1" :getTips2="popText2" :popBol="tipsBol"></my-pop>
	</div>
</template>

<script>

	import header3 from '../subComponents/header3'
	import loading from '../subComponents/loading'
	import littlePop from '../subComponents/littlePop'

	export default {
		data() {
			return {
				resoureUrl:"http://datainfo.duapp.com/shopdata/getGoods.php",
				returnData:[],
				loadingPic:false,
				popText1:"没有搜索到商品",
				popText2:"点击返回上一页",
				tipsBol:false
			}
		},
		components:{
			'my-header3':header3,
			'my-loading':loading,
			'my-pop':littlePop
		},		
		computed:{
			getClassID() {
				return this.$store.state.testIndex;
			},
			getText() {
				return this.$store.state.categoryChooseText[this.$store.state.testIndex].className;
			},
			getStateData() {
				return this.$store.state.goodsListPageData;
			}
		},
		methods:{
			getData() {
				this.loadingPic = true;
				this.$http.jsonp(this.resoureUrl,{
					params:{
						classID:this.getClassID+1 //加1才是对应的classID
					}
				}).then(function(res) {		
				console.log(res.body)			
					this.loadingPic = false;
					if(res.body === "0") {
						this.tipsBol = true;
					}
					this.$store.commit('goodsListPage',res.body);
				})
			},
			toGoodsDataList(index) {
				this.$store.commit('goodsIndex',index);
				this.$router.push('/goodsData');
			}
		},
		mounted() {
			this.getData()
		}
	}
</script>

<style scoped>
	.outerW {
		background-color: #f2efef;
		margin-top: 3.67rem;
		padding-top: 1rem;
	}
	.dataBox {
		margin:0 0.5rem;
	}
	.dataList {
		float: left;
		width:29.38%;
		text-align: center;
		margin:1rem 1.56% 0 1.56%;
	}
	.dataImg img {
		height:9.3rem;
	}
	.dataName {
		font-size:1.3rem;
		color: #676767;
		overflow: hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
		margin:0.7rem 0;
	}
	.dataPrice {
		font-size: 1rem;
		color: #e35386;
	}
</style>


