<template>
	<div class="popBox" v-if="endPopBol" @click="goBack">
		<div class="tipsWindow">
			<div class="subTipsWindow1">{{endGetTips1}}</div>
			<div class="subTipsWindow2">{{endGetTips2}}</div>
		</div>
	</div>
</template>

<script>
	export default {
		name:"popBox",
		props:["getTips1","getTips2","popBol"],
		computed:{
			endGetTips1:function() {
				return this.getTips1;
			},
			endGetTips2:function() {
				return this.getTips2;
			},
			endPopBol:function() {
				return this.popBol;
			}
		},
		methods:{
			goBack() {
				this.endPopBol = false;
				this.$router.push("/category");				
			}
		}
	}
</script>

<style>
	.popBox {
		position: fixed;
		top:0;
		left:0;
		width:100%;
		height:100%;
		background-color: rgba(0,0,0,.6);
	}
	.tipsWindow {
		position: absolute;
		top:0;
		right:0;
		bottom: 0;
		left:0;
		margin: auto;
		background-color: #e35386;
		color: #fff;
		text-align: center;
		width:43.75%;
		height:8rem;
		border-radius: 1rem;
		font-size: 2rem;		
	}
	.subTipsWindow1 {
		height:4rem;
		line-height: 5rem;
	}
	.subTipsWindow2 {
		height:4rem;
		line-height: 3rem;
	}
</style>


