<template>
	<div class="outerW" :style="{height:wid+'px'}">	
		<my-header1></my-header1>	
		<div class="category-main">

			<div class="category-left">
				<div class="categoryList" v-for="(item,index) in categoryList"  @click="getIndex(index)"  :class="{active:index === defaultIndex}">
					<span>{{ item }}</span>
				</div>			
			</div>

			<div class="category-right">
				<div class="clothing clearfix" v-if="categoryRight[0] === defaultIndex" >
					<span @click="getClassID(index)" v-for="(item,index) in clothingOne">
					{{item.className}}
					</span>					
				</div>
				<div class="clothing clearfix" v-if="categoryRight[1] === defaultIndex">
					<router-link to="/goodsList" @click.native="getText" v-for="(item,index) in clothingTwo">
					{{item}}
					</router-link>
				</div>
				<div v-if="categoryRight[2] === defaultIndex" class="clothing clearfix">
					测试3
				</div>
				<div v-if="categoryRight[3] === defaultIndex" class="clothing clearfix">
					测试4
				</div>
				<div v-if="categoryRight[4] === defaultIndex" class="clothing clearfix">
					测试5
				</div>
				<div v-if="categoryRight[5] === defaultIndex" class="clothing clearfix">
					测试6
				</div>
				<div v-if="categoryRight[6] === defaultIndex" class="clothing clearfix">
					测试7
				</div>
				<div v-if="categoryRight[7] === defaultIndex" class="clothing clearfix">
					测试8
				</div>
			</div>
		</div>
		<my-footer></my-footer>	
		<my-loading :loadingBol="loadingPic"></my-loading>	
	</div>
</template>

<script>

	import header1 from '../subComponents/header1'
	import footer from '../subComponents/footer'
	import loading from '../subComponents/loading'
	
	export default {
		name:"categoryList",
		data() {
			return {
				classNameUrl:"http://datainfo.duapp.com/shopdata/getclass.php",
				categoryList:["推荐","女装","男装","包包","美妆","奢侈","推荐","女装"],
				clothingOne:[],
				clothingTwo:["T恤","衬衫","针织衫","牛仔裤","半裙","连衣裙","短裤","家居服","POLO","小西装","小外套","卫衣","休闲装","打底裤","连衣裤","马甲","吊带","皮衣","针织裤","泳装","礼服","蝙蝠衫","背带裙"],
				categoryRight:[0,1,2,3,4,5,6,7],
				defaultIndex:0,
				loadingPic:false,
				wid: window.innerHeight-56,
			}
		},
		components:{
			'my-footer':footer,
			'my-header1':header1,
			'my-loading':loading
		},
		methods:{
			getIndex(i) {//tab切换
				this.defaultIndex = i;
			},
			getClassID(i) {
				this.$store.commit('changeIndex',i)//存储点击的index，其比classID的值小1
				this.$router.push("/goodsList")
			},
			getData() {//取回列表数据
				this.loadingPic = true;
				this.$http.get(this.classNameUrl)
					.then(function(res) {
						this.loadingPic = false;
						this.clothingOne = res.body;//当前页面循环用数据						
						this.$store.commit('changeText',res.body)//存储请求回来的列表数据
					})
			}
		},
		mounted() {
			this.getData();			
		}
	}
</script>

<style scoped>
	.outerW {
		background-color: #f2efef;
		margin-top: 3.67rem;
		padding-top: 1rem;
	}
	.category-main {
		display: flex;
		font-size: 1.5rem;
	}
	.category-left {
		width:23.13%;
	}
	.categoryList {
		height:5.5rem;
		display: flex;
		color: #909090;
		align-items:center;
		justify-content:center;
		border-right: 1px solid #dddddd;
	}
	.categoryList span {
		width:4.5rem;
		height:5.5rem;
		line-height: 5.5rem;		
		text-align: center;	
		border-bottom: 1px solid #dddddd;
		box-bottom-shadow: 0 0 3px #dddddd;	
	}
	.category-right {
		width:76.87%;
	}
	.clothing span {
		width:50%;
		height:4rem;
		line-height: 4rem;
		float: left;
		color: #909090;
		text-align: center;
	}
	.active {
		color: #d92f8a;
		border-right-color: #f2efef;
		border-top: 1px solid #ddd;
		border-bottom: 1px solid #ddd;
	}
</style>


