<template>
	<div class="outerW" :style="{height:wid+'px'}">
		<my-header2 :fatherPage="pageTitle"></my-header2>
		<div class="packagePicture" v-if="emptyBol">
			<img src="/static/img/emptyPic.png" alt="">
		</div>

		<div class="goodsData" v-if="showData">

			<div class="goodsPriceSum">
				<div class="goodsNumber">
					<span>商品数量：</span>
					<span>{{goodsData.length}}</span>
				</div>
				<div class="carriage">
					<span>应付总额(不含运费)：</span>
					<span>￥{{goodsSumPrice}}</span>
				</div>
			</div>

			<div class="goodsItem" v-for="(item,index) in goodsData">
				<div class="goodsImg">
					<img :src="item.goodsListImg" alt="">
				</div>

				<div class="goodsDescription">
					<p>
						<span>{{item.goodsName}}</span>
						<span @click="deleteGoods(index)">
							<img src="/static/img/deleteIco.png" alt="">
						</span>
					</p>
					<p>{{item.className}}</p>
					<p>
						<span>单价：</span>
						<span>￥{{item.price}}</span>
					</p>
					<p>
						<span>数量：</span>
						<span>
							<span class="decrease">-</span>
							<input type="number" :value="item.number">
							<span class="add">+</span>
						</span>
					</p>
				</div>
			</div>
		</div>

		<div class="hangout">
			<span @click="toCategory">逛一逛</span>
		</div>		
	</div>
</template>

<script>
	
	import header2 from '../subComponents/header2'

	export default {
		data() {
			return {
				pageTitle:"购物袋",
				wid: window.innerHeight-56,
				updateUrl:"http://datainfo.duapp.com/shopdata/getCar.php",
				emptyBol:false,
				showData:true,
				goodsData:[],
				goodsSumPrice:0
			}
		},
		components:{
			'my-header2':header2
		},
		computed:{
			getUserID:function() {
				return this.$store.state.userID;
			}
		},
		methods:{
			decrease(index) {
				
			},
			add() {

			},
			deleteGoods(i) {
				
			},
			toCategory() {
				this.$router.push('/category');
			}
		},
		mounted:function() {
			this.$http.jsonp(this.updateUrl,{
				params:{
					userID:this.getUserID
				}
			}).then(function(res) {
				console.log(res)
				this.goodsData = res.body;
				for(var i=0;i<res.body.length;i++) {
					this.goodsSumPrice += Number(res.body[i].price)
				}
				if(res.body === "0"||res.body === "2") {
					this.showData = false;
					this.emptyBol = true;
					return;
				}else {
					this.showData = true;
					this.emptyBol = false;
				}
			})
		}
	}
</script>

<style scoped>
	.outerW {		
		margin-top: 3.67rem;		
	}
	.packagePicture  {
		width:79.69%;
		height:13.3rem;
		margin: 8.8rem auto 0 auto;
	}
	.hangout {
		/*position: fixed;*/
		width:100%;
		height:4rem;
		/*bottom: 4rem;*/
		display: flex;
		justify-content:center;
	}
	.hangout span {
		width:67.19%;
		background-color: #fb0090;
		text-align: center;
		line-height: 4rem;
		color: #fff;
		font-size:2rem;
		border-radius: 0.5rem; 
	}
	.goodsData {
		margin-bottom: 10rem;
	}
	.goodsPriceSum {
		height:4.2rem;
		display: flex;
		align-items:center;
		background-color: #333333;
		color: #fff;
		font-size: 1.6rem;
	}
	.goodsNumber {
		width:40%
	}
	.goodsNumber span:nth-child(1) {
		margin-left: 1rem;
	}
	.carriage {
		width:60%;
	}
	.carriage span:nth-child(2) {
		color: #e63667;
	}
	.goodsItem {
		height:10rem;
		padding: 1rem;
		display: flex;
		border-bottom: 1px solid #cbcbcb;
	}
	.goodsImg {
		width:35.2%;
		margin-right: 1rem;
	}
	.goodsImg img {
		height:100%;
	}
	.goodsDescription {
		width:64.8%;
		font-size: 1.5rem;
	}
	.goodsDescription p {
		margin: 0.5rem;
	}
	.goodsDescription p:nth-child(1) {
		display: flex;
		justify-content:space-between;
	}
	.goodsDescription p:nth-child(1) span:nth-child(1) {
		height:1.5rem;
		overflow: hidden;
		text-overflow:ellipsis;
		white-space: nowrap;
	}
	.goodsDescription p:nth-child(1) span:nth-child(2) img{
		width:1.1rem;
		height:1.5rem;
	}
	.goodsDescription p:nth-child(3) {
		display: flex;
	}
	.goodsDescription p:nth-child(4) {
		display: flex;
		align-items:center;
	}
	.goodsDescription p:nth-child(4) span:nth-child(2) input {
		height:1.1rem;
		width:2rem;
		text-align: center;
	}
	.goodsDescription p:nth-child(4) span:nth-child(2) {
		display: flex;
	}
	.decrease,.add {
		width:2rem;
		height:1.5rem;
		line-height: 1.5rem;
		background-color: #828282;
		color: #fff;
		text-align: center;
	}
</style>


