<template>
	<div class="outerW" :style="{height:wid+'px'}">
		<my-header3 :fatherPage="pageTitle"></my-header3>
		<div class="details">
			<div class="detailsImg">
				<img :src="detailsImg" alt="">
			</div>
			<div class="descriptions">
				<p>{{brandName}}</p>
				<p v-for="(item,index) in infoText">
					<span>{{item.split("：")[0]}}：</span>
					<span>{{item.split("：")[1]}}</span>
				</p>				
			</div>
		</div>
		<my-loading :loadingBol="loadingPic"></my-loading>
	</div>
</template>

<script>

	import header3 from '../subComponents/header3'
	import loading from '../subComponents/loading'

	export default {
		data() {
			return {
				pageTitle:"商品详情",
				goodsDetailsUrl:"http://datainfo.duapp.com/shopdata/getGoods.php",
				detailsImg:"",
				brandName:"",
				infoText:[],
				loadingPic:false,
				wid: window.innerHeight-56,
			}
		},
		components:{
			'my-header3':header3,
			'my-loading':loading
		},
		computed:{
			showGoodsID:function() {
				return this.$store.state.goodsID;
			}
		},
		methods:{
			getData() {		
				this.loadingPic = true;
				this.$http.jsonp(this.goodsDetailsUrl,{
					params:{
						goodsID:this.showGoodsID
					}
				}).then(function(res) {
					this.loadingPic = false;
					this.brandName = res.body[0].goodsName;
					console.log(res.body[0].goodsName)
					this.detailsImg = JSON.parse(res.body[0].goodsBenUrl)[0];
					this.infoText = res.body[0].detail.split("；");					
				})
			}
		},
		mounted() {			
			this.getData();
		}
	}
</script>

<style scoped>
	.outerW {
		background-color: #f2efef;
		margin-top: 3.67rem;
		padding-top: 1rem;
	}
	.detailsImg {
		margin: 0 5%;
	}
	.descriptions {
		color: #8a8a8a;
		font-size: 1.3rem;
		margin: 1rem 5%;
	}
</style>


