<template>
	<div class="header">
		<div class="headerWrap clearfix">
			<a href="javascript:history.back();" class="brand">
				<img src="/static/img/returnIco.png" alt="">
			</a>		
			<span class="textBox">
				<span class="headerText">{{headerTitle}}</span>
			</span>			
			<router-link to="/cart" class="packageIco">
				<img src="/static/img/packageIco.png" alt="">
			</router-link>
			<router-link to="/login" class="userIco">
				<img src="/static/img/userIco.png" alt="">
			</router-link>
		</div>
	</div>
</template>

<script>
	export default {
		name:"header3",
		props:["fatherPage"],
		computed:{
			headerTitle() {
				return this.fatherPage
			}
		}
	}
</script>

<style scoped>
	.header {
		position: fixed;
		top:0;
		left:0;
		width:100%;
		height:3.67rem;
		background-color: #d2137a;
		display: flex;
		align-items:center;
	}
	.headerWrap {
		height:2.17rem;
		width:100%;
		position: relative;
	}	
	.brand {
		float: left;
		height:100%;
		margin: 0 1.42rem;
		display: flex;
		align-items:center;
	}
	.brand img {
		width:1.5rem;
		height:1.5rem;
	}
	.textBox {
		height:100%;
		width:40%;
		display: flex;
		align-items:center;
		justify-content:center;
		position: absolute;
		top:0;
		left:0;
		bottom:0;
		right:0;
		margin: auto;
	}
	.headerText {
		color: #fff;
		font-size: 1.83rem;
		line-height: 100%;
	}
	.userIco {
		height:100%;
		float:right;
		display: flex;
		align-items:flex-end;
	}
	.userIco img {
		width:1.58rem;
		height:1.83rem;
	}
	.packageIco {
		height:100%;
		float:right;
		display: flex;
		align-items:flex-end;
		margin: 0 1.33rem;
	}
	.packageIco img {
		width:1.58rem;
		height:1.83rem;
	}
</style>


