<template>
	<div class="outerW" :style="{height:wid+'px'}">
		<my-header1></my-header1>	
		<div class="moreInfo">
			<div class="items" v-for="(item,index) in itemsText" @click="telText(index)">
				<span> {{ item }} </span>
				<span> &gt; </span>
			</div>			
		</div>	
		<my-footer></my-footer>
	</div>
</template>

<script>

	import footer from '../subComponents/footer'
	import header1 from '../subComponents/header1'	


	export default {
		data() {
			return {
				itemsText:["信息中心","晒单分享","扫描二维码","系统设置","客服电话","软件更新","退货政策","关于我们"],
				wid: window.innerHeight-64,
			}
		},
		components:{
			'my-footer':footer,
			'my-header1':header1
		},
		methods:{
			telText(i) {
				this.$store.commit('setting',this.itemsText[i]);
				this.$router.push('/settings');
			}
		}
	}
</script>

<style scoped>
	.outerW {
		background-color: #f2efef;
		margin-top: 3.67rem;
		padding-top: 1rem;
	}
	.moreInfo {
		width:90.6%;
		margin: 0 auto;	
	}
	.items {
		height:5rem;
		line-height: 5rem;
		display: flex;
		justify-content:space-between;
		align-items:center;
		font-size: 1.5rem;
		background-color: #fff;
		margin:0.5rem 0;
		border-radius: 0.5 rem;
	}
	.items span:nth-child(1) {
		margin-left: 2rem;
	}
	.items span:nth-child(2) {
		margin-right: 1rem;
		font-family: "微软雅黑";
		color: #bbbbbb;
	}
</style>


