import Vue from 'vue'
import Router from 'vue-router'
// import Hello from '@/components/Hello'
import Home from '@/components/Pages/Home'
import Category from '@/components/Pages/Category'
import Search from '@/components/Pages/Search'
import More from '@/components/Pages/More'
import Cart from '@/components/Pages/Cart'
import Register from '@/components/Pages/Register'
import Login from '@/components/Pages/Login'
import GoodsList from '@/components/Pages/GoodsList'
import GoodsData from '@/components/Pages/GoodsData'
import GoodsDetails from '@/components/Pages/GoodsDetails'
import settings from '@/components/Pages/settings'
import searchResult from '@/components/Pages/searchResult'


Vue.use(Router)

export default new Router({
  routes: [
    {path: '/',name: 'Home',component: Home},
    {path: '/category',name: 'Category',component: Category},
    {path: '/search',name: 'Search',component: Search},
    {path: '/more',name: 'More',component: More},
    {path: '/cart',name: 'Cart',component: Cart},
    {path: '/register',name: 'Register',component: Register},
    {path: '/login',name: 'Login',component: Login},
    {path: '/goodsList',name: 'GoodsList',component: GoodsList},
    {path: '/goodsData',name: 'GoodsData',component: GoodsData},
    {path: '/goodsDetails',name: 'GoodsDetails',component: GoodsDetails},
    {path: '/settings',name: 'settings',component: settings},
    {path: '/searchResult',name: 'searchResult',component: searchResult}
  ]
})



