<template>
	<div class="outerW" :style="{height:wid+'px'}">
		<my-header1></my-header1>
		<div class="searchMain">
			<div class="searchInput">
				<input type="text" placeholder="请输入搜索内容" v-model.trim="userInput">
				<span @click="toSearch">搜索</span>
			</div>
			<div class="fastSearch">
				<span>印花短裙</span>
				<span>高腰短裤</span>
				<span>女装</span>
				<span>爱马仕</span>
				<span>优衣库</span>
				<span>天意美</span>
				<span>芬狄诗</span>
				<span>玛丽古椅</span>
			</div>
		</div>
		<my-loading :loadingBol="loadingPic"></my-loading>	
		<my-footer></my-footer>
	</div>
</template>

<script>

	import footer from '../subComponents/footer'
	import header1 from '../subComponents/header1'
	import loading from '../subComponents/loading'

	export default {
		data() {
			return {
				userInput:"",
				searchUrl:"http://datainfo.duapp.com/shopdata/selectGoodes.php",
				wid: window.innerHeight-64,
				loadingPic:false
			}
		},
		components:{
			'my-footer':footer,
			'my-header1':header1,
			'my-loading':loading
		},
		methods:{
			toSearch() {
				// this.$store.commit('SearchGoods',this.userInput)
				if(!this.userInput) {
					alert("请输入商品名称！");
					return;
				}
				this.loadingPic = true;
				this.$http.jsonp(this.searchUrl,{
					params:{
						selectText:encodeURIComponent(this.userInput)
					}
				}).then(function(res) {
					console.log(res.body);
					this.loadingPic = false;
					this.$store.commit('SearchGoods',res.body);
					this.$router.push('/searchResult');
				})
			}
		}
	}
</script>

<style scoped>
	.outerW {
		background-color: #f2efef;
		margin-top: 3.67rem;
		padding-top: 1rem;
	}
	.searchInput {
		height:3.5rem;
		display: flex;
		justify-content:center;
		align-items:center;
		font-size: 1.3rem;
		padding-top: 1.5rem;
	}
	.searchInput input {
		height:3.5rem;
		width:63.12%;
		border-radius: 0.25rem;
		outline:none;
		border: 1px solid #cccccc;
		background: url('/static/img/search.png') no-repeat;
		background-size: 2.1rem 2.1rem;
		background-position: 0.8rem center;
		padding-left: 3.7rem;
		background-color: #fff;
	}
	.searchInput span {
		height:3.5rem;
		line-height: 3.5rem;
		width:19.38%;
		background-color: #ed0085;
		text-align: center;
		color: #fff;
		border: 2px solid #ed0085;
		border-radius: 0.25rem;
	}
	.fastSearch {
		position: relative;
	}
	.fastSearch span {
		position: absolute;
		font-size: 2rem;
		font-weight: bolder;
	}
	.fastSearch span:nth-child(1) {
		top:4.5rem;
		left:3.5rem;
		color: #ff84ca;
	}
	.fastSearch span:nth-child(2) {
		top:6.2rem;
		right:7.5rem;
		color: #fd75c2;
	}
	.fastSearch span:nth-child(3) {
		top:11rem;
		left:7.4rem;
		color: #ff548c;
	}
	.fastSearch span:nth-child(4) {
		top:13rem;
		right:8.7rem;
		color: #f7c7e1;
	}
	.fastSearch span:nth-child(5) {
		top:17.5rem;
		left:3.8rem;
		color: #fe8bcc;
	}
	.fastSearch span:nth-child(6) {
		top:18.3rem;
		right:11.3rem;
		color: #e2c4d5;
	}
	.fastSearch span:nth-child(7) {
		top:25rem;
		left:6.3rem;
		color: #ff84ca;
	}
	.fastSearch span:nth-child(8) {
		top:24.1rem;
		right:2.5rem;
		color: #ffa4cd;
	}
</style>


