<template>
	<div class="outerW" :style="{height:wid+'px'}">
		<my-header2 :fatherPage="pageTitle"></my-header2>
		<div class="logIn">
			<div class="userCount">
				<span>帐号名称：</span>
				<input type="text" placeholder="请输入手机号" v-model.trim="count" @blur="showCountTips">
				<span v-if="countTipsBol">*{{countTips}}</span>
			</div>
			<div class="userPassword">
				<span>登录密码：</span>
				<input type="password" placeholder="请输入密码" v-model="password1" @blur="showPasswordTips1">
				<span v-if="passwordBol1">*{{passwordTips1}}</span>
			</div>
			<div class="userPasswordAgain">
				<span>确认密码：</span>
				<input type="password" placeholder="请输入密码" v-model="password2" @blur="showPasswordTips2">
				<span v-if="passwordBol2">*{{passwordTips2}}</span>
			</div>
			<div class="agreement">
				<input type="checkbox">
				<span>我已阅读并接受走秀网服务条款</span>
			</div>	
			<div class="register">
				<span @click="toRegister">注册</span>
			</div>
		</div>
		<my-loading :loadingBol="loadingPic"></my-loading>
		<div class="registerPop" v-if="registerPopBol" @click="advancedEnd">
			<div class="registerTips">
				<span>{{registerTips}}</span>
				<span @click="cancelPop">+</span>
			</div>
		</div>
	</div>
</template>

<script>

	import header2 from '../subComponents/header2'
	import loading from '../subComponents/loading'

	export default {
		data() {
			return {
				pageTitle:"注册",
				wid: window.innerHeight-56,
				count:"",
				countTips:"",
				countTipsBol:false,
				password1:"",
				password2:"",
				passwordTips1:"",
				passwordBol1:false,
				passwordTips2:"",
				passwordBol2:false,
				registerUrl:"http://datainfo.duapp.com/shopdata/userinfo.php",
				loadingPic:false,
				registerTips:"",
				registerPopBol:false,
				timer:0,
				num:5
			}
		},
		components:{
			'my-header2':header2,
			'my-loading':loading
		},
		methods:{
			showCountTips() {
				if(!this.count) {
					this.countTips = "手机号不能为空！";
					this.countTipsBol = true;
				}else{
					var pattern = /^1[3|4|5|7|8][0-9]{9}$/;
					if(!pattern.test(this.count)){
						this.countTips = "请正确输入手机号！";
					}else{
						this.countTipsBol = false;
					}
				}
			},
			showPasswordTips1() {
				if(!this.password1) {
					this.passwordTips1 = "密码不能为空！";
					this.passwordBol1 = true;
				}else{
					this.passwordBol1 = false;
				}
			},
			showPasswordTips2() {
				if(!this.password2) {
					this.passwordTips2 = "密码不能为空！";
					this.passwordBol2 = true;
				}else if(this.password1 !== this.password2){
					this.passwordTips2 = "两次密码不一样！";
					this.passwordBol2 = true;
				}else{
					this.passwordBol2 = false;
				}
			},
			success() {
				this.num--;
				this.registerTips = "成功!"+this.num+"秒后跳转登录";
				if(this.num === 0) {
					this.registerPopBol = false;
					this.num = 5;
					clearInterval(this.timer)
					this.$router.push("/login")
				}
			},
			advancedEnd() {
				this.registerPopBol = false;
				clearInterval(this.timer);
			},
			toRegister() {
				if(!this.count||!this.password1||!this.password2||this.countTipsBol||this.passwordBol1||this.passwordBol2) {
					this.registerPopBol = true;
					this.registerTips = "请检查输入信息!";
				}else{
					this.loadingPic = true;
					this.$http.get(this.registerUrl,{
						params:{
							status:"register",
							userID:this.count,
							password:this.password1
						}
					}).then(function(res) {
						if(res.body == "0") {
							this.registerTips = "用户名已存在，请重试！"
						}else if(res.body == "1") {
							this.timer = setInterval(this.success,1000);
							this.registerTips = "成功!"+this.num+"秒后跳转登录";
						}else{
							this.registerTips = "数据库报错！请重试！"
						}
						this.loadingPic = false;
						this.registerPopBol = true;
						console.log(res.body)
					})
				}
			},
			cancelPop() {
				this.registerPopBol = false;
			}
		}
	}
</script>

<style scoped>
	.outerW {
		background-color: #f2efef;
		margin-top: 3.67rem;
		padding-top: 1rem;
	}
	.logIn {
		padding-top: 1.5rem;
	}
	.userCount {
		position: relative;
		margin-bottom: 2.2rem;
		display: flex;
		align-items:center;
		justify-content:center;
	}
	.userCount span:nth-child(1) {
		height:4.3rem;
		line-height: 4.3rem;
		font-size: 1.6rem;
		background-color: #fff;
		padding-left:0.5rem;
		border-top-left-radius: 0.5rem;
		border-bottom-left-radius: 0.5rem;
	}
	.userCount span:nth-child(3) {
		position: absolute;
		left:8%;
		top:4.6rem;
		color: #ff0f53;
	}
	.userCount input {
		width:67.19%;
		height:4.3rem;
		outline: none;
		border: none;
		border-top-right-radius: 0.5rem;
		border-bottom-right-radius: 0.5rem;
		font-size: 1.6rem;
		color: #646464;
	}
	.userPassword, .userPasswordAgain {
		position: relative;
		margin-bottom: 2.2rem;
		display: flex;
		align-items:center;
		justify-content:center;
	}
	.userPassword span:nth-child(3),.userPasswordAgain span:nth-child(3) {
		position: absolute;
		left:8%;
		top:4.6rem;
		color: #ff0f53;
	}
	.userPassword span:nth-child(1),.userPasswordAgain span:nth-child(1) {
		height:4.3rem;
		line-height: 4.3rem;
		font-size: 1.6rem;
		background-color: #fff;
		padding-left:0.5rem;
		border-top-left-radius: 0.5rem;
		border-bottom-left-radius: 0.5rem;
	}
	.userPassword input,.userPasswordAgain input {
		width:67.19%;
		height:4.3rem;
		outline: none;
		border: none;
		border-top-right-radius: 0.5rem;
		border-bottom-right-radius: 0.5rem;
		font-size: 1.6rem;
		color: #646464;
	}
	.agreement {
		padding-left: 8%;
		height:2.2rem;
		margin: 2rem 0;
		font-size: 1.5rem;
		display: flex;
		align-items:center;
	}
	.agreement input {
		width:1.5rem;
		height:1.5rem;
	}
	.agreement span {
		color: #ff0f53;
		margin-left: 0.5rem;
	}
	.register {
		display: flex;
		align-items:center;
		justify-content:center;
		margin: 0.5rem 0;
	}
	.register span {
		width:90.63%;
		height:4.4rem;
		text-align: center;
		line-height: 4.4rem;
		background-color: #ef0086;
		color: #fff;
		font-size: 2rem;
		border-radius: 0.5rem;
	}
	.registerPop {
		position: fixed;
		left:0;
		top:0;
		width:100%;
		height:100%;
		background-color: rgba(0,0,0,.6);
	}
	.registerTips {
		position: absolute;
		top:0;
		right:0;
		bottom: 0;
		left:0;
		margin: auto;
		width:16rem;
		height:6rem;
		background-color: #ef0086;
		color: #fff;
		text-align: center;
		font-size: 1.6rem;
		border-radius: 0.8rem;
		display: flex;
		align-items:center;
	}
	.registerTips span:nth-child(1) {
		width:100%;
		text-align: center;
	}
	.registerTips span:nth-child(2) {
		position: absolute;
		top:0;
		right:0;
		width:2rem;
		height:2rem;
		border-radius: 1rem;
		background-color: #ff7f00;
		font-size: 2rem;
		line-height: 2rem;
		transform:rotate(45deg);
	}
</style>


