<template>
	<div class="outerW" :style="{height:wid+'px'}">
		<my-header3 :fatherPage="getText"></my-header3>
	</div>
</template>

<script>


	import header3 from '../subComponents/header3'


	export default {
		data() {
			return{
				wid: window.innerHeight-56
			}
		},
		computed:{
			getText:function() {
				return this.$store.state.settingText;
			}
		},
		components:{
			'my-header3':header3
		}
	}
</script>

<style>
	.outerW {
		background-color: #f2efef;
		margin-top: 3.67rem;
		padding-top: 1rem;
	}
	
</style>


