<template>
	<div class="footer" id="footer">
		<router-link :to="toPageRoute[index]" v-for="(item,index) in footerText" v-bind:class="{active:$route.path === toPageRoute[index]}">
			{{ item }}
		</router-link>		
	</div>
</template>

<script>
	export default {
		name:"footer",
		data() {
			return {
				footerText:["首页","分类","搜索","更多"],
				toPageRoute:["/","/category","/search","/more"]
			}
		}
	}
</script>

<style>
	.footer {
		position: fixed;
		left:0;
		bottom: 0;
		width:100%;
		height:4rem;
		background-color: #51494e;
		display: flex;
	}
	.footer a {
		flex:1;
		height:100%;
		line-height: 6.2rem;
		color:#fff;
		font-size: 1rem;
		text-align: center;
	}
	.footer a:nth-child(1) {
		background: url("/static/img/homeIco.png") no-repeat;
		background-size:2rem 2rem;
		background-position:center 0.4rem;		
	}
	.footer a:nth-child(2) {
		background: url("/static/img/categoryIco.png") no-repeat;
		background-size:2rem 2rem;
		background-position:center 0.4rem;		
	}
	.footer a:nth-child(3) {
		background: url("/static/img/searchIco.png") no-repeat;
		background-size:2rem 2rem;
		background-position:center 0.4rem;		
	}
	.footer a:nth-child(4) {
		background: url("/static/img/moreIco.png") no-repeat;
		background-size:2rem 2rem;
		background-position:center 0.4rem;		
	}
	#footer .active {
		background-color: #d01179 ;
	}
</style>


