<template>
	<div class="header">
		<div class="headerWrap clearfix">
			<a href="javascript:history.back();" class="headerBack">
				<img src="/static/img/returnIco.png" alt="">
			</a>
			<span class="headerTitle">
				{{ headerTitle }}
			</span>
		</div>
	</div>
</template>

<script>
	export default {
		name:"header2",
		props:["fatherPage"],
		computed:{
			headerTitle() {
				return this.fatherPage
			}
		}
	}
</script>

<style scoped>
	.header {
		position: fixed;
		top:0;
		left:0;
		width:100%;
		height:3.67rem;
		background-color: #d2137a;
		display: flex;
		align-items:center;
	}
	.headerWrap {
		width:100%;
		height:1.67rem;
		display: flex;
		align-items:center;
		justify-content:space-between;
	}	
	.headerWrap:after {
		content: "";
		display: block;
		height:1rem;
		width:1rem;
		margin-right: 1.42rem;
	}
	.headerBack {
		margin-left: 1.42rem;
	}
	.headerBack img {
		width:1.5rem;
		height:1.5rem;
	}
	.headerTitle {
		color: #fff;
		font-size: 2rem;
	}
</style>


