<template>
	<div class="outerW" :style="{height:wid+'px'}">
		<my-header2 :fatherPage="pageTitle"></my-header2>
		<div class="logIn">
			<div class="userCount">
				<input type="text" placeholder="请输入手机号" v-model.trim="count" @blur="showCountTips">
				<span v-if="countTipsBol">*{{countTips}}</span>
			</div>
			<div class="userPassword">
				<input type="password" placeholder="请输入密码" v-model="password" @blur="showPasswordTips" v-if="seePassword">
				<input type="text" placeholder="请输入密码" v-if="hidePasswor" v-model="password">
				<span v-if="passwordBol">*{{passwordTips}}</span>
			</div>
			<div class="aboutPassword">
				<input type="checkbox" @click="showPasswordOrNot">
				<span>显示密码</span>
				<span @click="changePassword">忘记密码？</span>
			</div>
			<div class="rememberPassword">
				<input type="checkbox">
				<span>记住密码自动登录</span>
			</div>
			<div class="login">
				<span @click="toLogin">登录</span>
			</div>
			<div class="register">
				<span @click="toRegister">注册</span>
			</div>
		</div>
		<my-loading :loadingBol="loadingPic"></my-loading>
		<div class="loginPop" v-if="loginPopBol" @click="advancedEnd">
			<div class="loginTips">
				<span>{{loginTips}}</span>
				<span @click.stop="cancelPop">+</span>
			</div>
		</div>
	</div>
</template>

<script>

	import header2 from '../subComponents/header2'
	import loading from '../subComponents/loading'

	export default {
		data() {
			return {
				pageTitle:"登录",
				wid: window.innerHeight-56,
				count:"",
				countTipsBol:false,
				countTips:"",
				password:"",
				seePassword:true,
				hidePasswor:false,
				passwordTips:"",
				passwordBol:false,
				loginUrl:"http://datainfo.duapp.com/shopdata/userinfo.php",
				loadingPic:false,
				loginPopBol:false,
				loginTips:"",
				num:3,
				timer:0
			}
		},
		components:{
			'my-header2':header2,
			'my-loading':loading
		},
		methods:{
			showCountTips() {
				if(!this.count) {
					this.countTips = "手机号不能为空！";
					this.countTipsBol = true;
				}else{
					var pattern = /^1[3|4|5|7|8][0-9]{9}$/;
					if(!pattern.test(this.count)){
						this.countTipsBol = true;
						this.countTips = "请正确输入手机号！";
					}else{
						this.countTipsBol = false;
					}
				}
			},
			showPasswordTips() {
				if(!this.password) {
					this.passwordTips = "密码不能为空！";
					this.passwordBol = true;
				}else{
					this.passwordBol = false;
				}
			},
			showPasswordOrNot() {
				if(this.seePassword == true) {
					this.seePassword = false;
					this.hidePasswor = true;
				}else{
					this.hidePasswor = false;
					this.seePassword = true;
				}
			},
			success() {
				this.num--;
				this.loginTips = "成功!"+this.num+"秒后跳转登录";
				if(this.num === 0) {
					this.loginPopBol = false;
					this.num = 3;
					clearInterval(this.timer)
					this.$router.push("/")
				}
			},
			toLogin() {
				if(!this.count||!this.password||this.countTipsBol||this.passwordBol) {
					this.loginPopBol = true;
					this.loginTips = "请检查输入！"
				}else{
					this.loadingPic = true;
					this.$http.get(this.loginUrl,{
						params:{
							status:"login",
							userID:this.count,
							password:this.password
						}
					}).then(function(res) {
						console.log(res.body.userID)
						this.$store.commit('saveUserID',res.body.userID);
						if(res.body == "0") {
							this.loginTips = "用户名不存在，请先注册！"
						}else if(res.body == "2") {
							this.loginTips = "用户名密码不符合,请重试!";
						}else{
							this.$store.commit('saveUserID',this.count);
							this.loadingPic = false;
							this.loginTips = "成功!"+this.num+"秒后跳转首页";
							this.timer = setInterval(this.success,1000);
						}						
						this.loginPopBol = true;		
					})
				}
			},
			toRegister() {
				this.$router.push("/register");
			},
			changePassword() {
				alert("开发中");
			},
			advancedEnd() {
				this.loginPopBol = false;
			},
			cancelPop() {
				this.loginPopBol = false;
			}
		}
	}
</script>

<style scoped>
	.outerW {
		background-color: #f2efef;
		margin-top: 3.67rem;
		padding-top: 1rem;
	}
	.logIn {
		padding-top: 1.5rem;
	}
	.userCount {
		position: relative;
		margin-bottom: 2.2rem;
	}
	.userCount input {
		width:86.63%;
		height:4.3rem;
		padding-left: 4%;
		outline: none;
		border: none;
		border-radius: 0.5rem;
		display: block;
		margin: 0 auto;
		font-size: 1.6rem;
		color: #646464;
	}
	.userCount span:nth-child(2) {
		position: absolute;
		left:8%;
		top:4.6rem;
		color: #ff0f53;
	}
	.userPassword {
		position: relative;
		margin-bottom: 3.2rem;
	}
	.userPassword span:nth-child(2) {
		position: absolute;
		left:8%;
		top:4.6rem;
		color: #ff0f53;
	}
	.userPassword input {
		width:86.63%;
		height:4.3rem;
		padding-left: 4%;
		outline: none;
		border: none;
		border-radius: 0.5rem;
		display: block;
		margin: 0 auto;
		font-size: 1.6rem;
		color: #646464;
	}
	.aboutPassword {
		padding-left: 8%;
		height:2.2rem;
		margin: 1rem 0;
		font-size: 1.5rem;
		display: flex;
		align-items:center;
	}
	.aboutPassword input {
		width:1.5rem;
		height:1.5rem;
	}
	.aboutPassword span:nth-child(2) {
		margin:0 2rem 0 0.5rem;
		color: #646464;
	}
	.aboutPassword span:nth-child(3) {
		color: #ff0f53;
	}
	.rememberPassword {
		padding-left: 8%;
		height:2.2rem;
		margin: 1rem 0;
		font-size: 1.5rem;
		display: flex;
		align-items:center;
	}
	.rememberPassword span:nth-child(2) {
		margin-left:0.5rem;
		color: #646464;
	}
	.rememberPassword input {
		width:1.5rem;
		height:1.5rem;
	}
	.login {
		display: flex;
		align-items:center;
		justify-content:center;
		margin: 0.5rem 0;
	}
	.login span {
		width:90.63%;
		height:4.4rem;
		text-align: center;
		line-height: 4.4rem;
		background-color: #ef0086;
		color: #fff;
		font-size: 2rem;
		border-radius: 0.5rem;
	}
	.register {
		display: flex;
		align-items:center;
		justify-content:center;
	}
	.register span {
		width:90.63%;
		height:4.4rem;
		line-height: 4.4rem;
		text-align: center;
		background-color: #ef0086;
		color: #fff;
		font-size: 2rem;
		border-radius: 0.5rem;
	}
	.loginPop {
		position: fixed;
		left:0;
		top:0;
		width:100%;
		height:100%;
		background-color: rgba(0,0,0,.6);
	}
	.loginTips {
		position: absolute;
		top:0;
		right:0;
		bottom: 0;
		left:0;
		margin: auto;
		width:16rem;
		height:6rem;
		background-color: #ef0086;
		color: #fff;
		text-align: center;
		font-size: 1.6rem;
		border-radius: 0.8rem;
		display: flex;
		align-items:center;
	}
	.loginTips span:nth-child(1) {
		width:100%;
		text-align: center;
	}
	.loginTips span:nth-child(2) {
		position: absolute;
		top:0;
		right:0;
		width:2rem;
		height:2rem;
		border-radius: 1rem;
		background-color: #ff7f00;
		font-size: 2rem;
		line-height: 2rem;
		transform:rotate(45deg);
	}
</style>


