// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Resource from 'vue-resource'
import Vuex from 'vuex'
Vue.use(Resource)
Vue.use(Vuex)
import App from './App'
import router from './router'


Vue.config.productionTip = false

import Basecss from '../static/css/base.css'

/* eslint-disable no-new */
const store = new Vuex.Store({
	state:{
		categoryChooseText:{},//分类商品名称数组
		testIndex:0,//对应商品名称的index
		clasifyGoods:{},//分类商品数组
		goodsListPageData:{},//商品资料页的goods对象
		goodsListPageIndex:0,//选择的商品下标，可以根据此再请求其具体数据
		goodsID:{},//
		settingText:"",
		userID:"",
		searchResult:{}
	},
	mutations:{
		changeText(state,i) {//classID
			state.categoryChooseText = i;
		},
		changeIndex(state,i) {
			state.testIndex = i;
		},
		goodsListPage(state,i) {
			state.goodsListPageData = i;
		},
		goodsIndex(state,i) {
			state.goodsListPageIndex = i;
		},
		getGoodsID(state,i) {
			state.goodsID = i;
		},
		setting(state,i) {
			state.settingText = i;
		},
		saveUserID(state,i) {
			state.userID = i;
		},
		SearchGoods(state,i) {
			state.searchResult = i;
		}
	}
	
})
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



